import React from 'react';
import Button from '../Button/Button';
import { ReactComponent as Smile } from '../../assets/smile.svg';

function Question(props) {

  const handleAnswer = (event) => {
    event.preventDefault();
    props.handleAnswer(event);
    props.history.push(`/result`);
  };

  const handleChange = (event) => {
    props.handleChange(event);
  };

  const { question } = props;
  const { answers } = question;


  return (
    <React.Fragment>
      <div className="test-wrapper">
        <h1 className="test-title">Привет, это логическая игра от SSA Group
          <span className="icon-smile"><Smile/></span>
        </h1>
        <p className="test-subtitle">Тебе нужно решить всего одну задачу. <br/>
          В случае правильного ответа - тебя ждет сюрприз.
          В случае неверного - попробуй еще раз=)
          Начнем?</p>
        <p className="test-question">{question.text}</p>
        <form className="form-question" onSubmit={handleAnswer}>
          <div className="answer-group">
            {answers.map((answer, i) => {
              return <Button key={answer.text}
                             id={i + 1} type="radio"
                             value={answer.text} name="choice"
                             handleChange={handleChange}
              />
            })}
          </div>
          <div className="row-btn">
            <button className="btn btn-danger btn-test"
                    type="submit">
              Ответить
            </button>
          </div>
        </form>

      </div>
    </React.Fragment>
  );
}

export default Question;
