import React, { Component } from 'react';
import { HashRouter as Router, Route } from "react-router-dom";
import Header from '../Header/Header';
import Start from '../Start/Start';
import Question from '../Question/Question';
import Result from '../Result/Result';
import { questions } from '../../quizData';
import { shuffle } from '../../helpers/helpers';

const initialState = {
  isCorrect: false,
  question: shuffle(questions)[0],
  choice: null
};

export default class Quiz extends Component {
  constructor(props) {
    super(props);
    this.state = initialState;

    this.handleChange = this.handleChange.bind(this);
    this.handleAnswer = this.handleAnswer.bind(this);
    this.handleStart = this.handleStart.bind(this);
  }

  handleAnswer() {
    const { choice, question } = this.state;
    const { answers } = question;

    let result = answers.find(answer => answer.text === choice);
    this.setState({
      isCorrect: result ? result.isCorrect : false
    })
  }

  handleChange(event) {
    const target = event.target;
    const value = target.value;
    const name = target.name;

    this.setState({
      [name]: value
    });
  }

  handleStart() {
    this.setState({
      isCorrect: false,
      question: shuffle(questions)[0],
      choice: null
    });
  }

  render() {
    const { question, isCorrect } = this.state;

    return (
      <React.Fragment>
        <Router>
          <div className="quiz content">
            <Header/>
            <div className="container">
              <Route exact path="/" component={Start}/>
              <Route path="/question"
                     render={props => <Question {...props}
                                                question={question}
                                                handleChange={this.handleChange}
                                                handleAnswer={this.handleAnswer}/>

                     }/>
              <Route path="/result"
                     render={props => <Result {...props}
                                              isCorrect={isCorrect}
                                              handleStart={this.handleStart}/>
                     }/>
            </div>
          </div>
          <footer className="footer"/>
        </Router>
      </React.Fragment>
    );
  }
}


