import React from 'react';
import { ReactComponent as Smile } from '../../assets/smile.svg';
import { ReactComponent as SmileSad } from '../../assets/smile_.svg';

function Result(props) {
  const { isCorrect } = props;
  const handleStart = () => {
    props.handleStart();
    props.history.push(`/`);
  };

  return (
    <React.Fragment>
      <div className="test-wrapper">
        {isCorrect ? (
          <React.Fragment>
            <h1 className="test-title">Вы ответили верно
              <span className="icon-smile"><Smile/></span>
            </h1>
            <h2 className="test-subtitle">Получите свой подарок от SSA GROUP</h2>
          </React.Fragment>
        ) : (
          <React.Fragment>
            <h1 className="test-title">Ответ не верный</h1>
            <div className="smile-row">
              <span className="icon-smile"><SmileSad/></span>
              <span className="icon-smile"><SmileSad/></span>
              <span className="icon-smile"><SmileSad/></span></div>
          </React.Fragment>
        )}
        <button className="btn btn-danger btn-test" onClick={handleStart}>НА ГЛАВНУЮ</button>
      </div>
    </React.Fragment>
  );
}

export default Result;
