import React from 'react';

function Button(props) {
  const { id, value, name, type } = props;

  const handleChange = (event) => {
    props.handleChange(event);
  };

  return (
    <div className="answer">
      <input id={id}
             className="input-answer"
             type={type}
             value={value}
             name={name}
             onChange={handleChange}/>
      <label htmlFor={id} className="label-answer">
        <span className="label-answer-num">{id}</span>
        <span className="label-answer-txt">{value}</span>
      </label>
    </div>
  );
}

export default Button;
